body{
    background: linear-gradient(180deg, rgba(81,81,99,1) 0%, rgba(0,194,255,1) 100%);
    min-height: 100vh;
}

p{
    color: white;
}

i{
    color: white;
}

h1{
    color: white;
}

form{
    display: flex;
    justify-content: center;
}

input{
    padding-top: 7px;
    padding-bottom: 7px;
    border: none;
    border-radius: 6px;
    font-weight: bold;
}

button {
	box-shadow: 0px 1px 0px 0px #276873;
	background:linear-gradient(to bottom, #599bb3 5%, #408c99 100%);
	background-color:#599bb3;
	border-radius:6px;
	border:1px solid #29668f;
	display:inline-block;
	cursor:pointer;
	color:#ffffff;
	font-family:Arial;
	font-size:15px;
	font-weight:bold;
	padding:6px 24px;
	text-decoration:none;
	text-shadow:0px 1px 0px #326e82;
}

button:hover {
	background:linear-gradient(to bottom, #408c99 5%, #599bb3 100%);
	background-color:#408c99;
}

button:active {
	position:relative;
	top:1px;
}


nav{
    display: flex;
    justify-content: space-around;
    align-items: center;
    min-height: 10vh;
}

.nav-links{
    width: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    list-style: none;
    padding: 0;
}

.nav-links a{
	box-shadow: 0px 1px 0px 0px #276873;
	background:linear-gradient(to bottom, #599bb3 5%, #408c99 100%);
	background-color:#599bb3;
	border-radius:6px;
	border:1px solid #29668f;
	display:inline-block;
	cursor:pointer;
	color:#ffffff;
	font-family:Arial;
	font-size:15px;
	font-weight:bold;
	padding:6px 24px;
	text-decoration:none;
	text-shadow:0px 1px 0px #326e82;
}

.nav-links a:hover {
	background:linear-gradient(to bottom, #408c99 5%, #599bb3 100%);
	background-color:#408c99;
}

.nav-links a:active {
	position:relative;
	top:1px;
}

.wi-flip-horizontal{
    transform: scale(2, 2);
}

.forecastTitle{
    margin-top: 2em;
}

.days{
    display: flex;
    justify-content: space-around;
    margin-top: 2em;
}

.day{
    background-color: rgba(255, 255, 255, 0.6);
    border-radius: 6px;
    padding: 10px;
    width: 250px;
}

.day p{
    color: rgb(81,81,99,1);
}

.day i{
    color: rgb(81,81,99,1);
}

@media only screen and (max-width: 1200px){
    .days{
        flex-direction: column;
    }

    form{
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        max-width: 60%;
        margin: auto;
    }

    input{
        margin-bottom: 1em;
        text-indent: 15px;
    }

    input::placeholder{
        text-indent: 15px;
    }
    .day{
        margin: auto;
        margin-bottom: 1em;
    }

}
